   .audio-player {
    background: #9b6955;
    max-width: 90%;
  }
  
  .inner {
    margin: 0 auto;
    padding: 20px;
  }
  
  /* audio info */
  .audio-info {
    display: flex;
    gap: 20px;
  }
  
  .author-text {
    color: #0a0093;
  }
  
  .songtitle {
    color: #ff0000db;
    font-weight: 100;
    font-size: 24px;
    margin-bottom: 0;
    padding: 2px;
    line-height: 37px;
    background-color: #bed01b;
  }
  
  /* audio image */
  .icon-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  
  .audio-image {
    width: 300px;
    height: 300px;
    background: #1e1e1e; 
  }
  
  .audio-icon {
    font-size: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    width: 80px;
    border-radius: 50%;
    background: #989898;
  }
  
  .audio-image img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    vertical-align: middle;
  }
  
  /* controls */
  .controls-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
  }
  
  .controls button, .volume button {
    border: none;
    background-color: transparent;
    margin-right: 8px;
    cursor: pointer;
  }
  
  button svg {
    font-size: 20px;
  }
  
  .volume {
    display: flex;
    align-items: center;
  }
  
  .volume button {
    margin: 0;
  }
  
  .progress {
    display: flex;
    align-items: center;
    gap: 10px;
    width: 100%;
  }
  
  /* time */
  .time {
    color: #333;
    font-size: 13px;
    line-height: 46px;
  }
  
  .time.current {
    color: #f50;
  }
  